  gettingInvoiceDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewCustomerDetails/' + this.invoiceNum, options)
      .subscribe(data => {
        var response = data.json();
        //console.log(response)
        if (response.status == 200) {
          this.invoiceView = response.response[0]
          console.log("invoiceView" + JSON.stringify(this.invoiceView))
          if (this.invoiceView.proImage == '../../assets/img/no-image.png') {
            this.invoiceView.proImage = this.invoiceView.proImage
          } else {
            this.invoiceView.proImage = Global.url + this.invoiceView.proImage
          }

          this.invoiceViewResponse = true;
          this.InvoiceLabourDetails()

        }
        else {
          this.invoiceViewResponse = false;
        }
        error => {
        }
      })
  }

  invoicePaymentDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getInvoicePaidAmount/' + this.invoiceNum, options)
      .subscribe(data => {
        var payData = data.json();
        if (payData.status == 200) {
          this.paymentData = payData.response[0]
          console.log("paymentData" + JSON.stringify(this.paymentData))
          this.paidData = this.paymentData.paidAmount
          this.payResponseStatus = true;
        } else {
          this.paidData = 0
          this.payResponseStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }

  InvoiceLabourDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewCustomerLaborDetails/' + this.invoiceNum, options)
      .subscribe(data => {
        var response = data.json();
        //console.log(response)
        if (response.status == 200) {
          this.finalArray = response.response
          console.log("finalArray" + JSON.stringify(this.finalArray))
          this.invoiceViewResponse = true;
          //this.invoicePaymentDetails()
        }
        else {
          this.invoiceViewResponse = false;
        }
        error => {
        }
      })
  }
