  paymentTypeSelection(selectedPaymentType) {
    console.log(selectedPaymentType)
    if (selectedPaymentType == "CHEQUE") {
      this.chequeDetails = true;
      this.creditCardDetails = false;
    } else if (selectedPaymentType == "CREDITCARD") {
      this.creditCardDetails = true
      this.chequeDetails = false;
    } else if (selectedPaymentType == "CASH") {
      this.creditCardDetails = false
      this.chequeDetails = false;
    } else if (selectedPaymentType == "APPLEPAY") {
      this.creditCardDetails = false
      this.chequeDetails = false;
    } else if (selectedPaymentType == "VENMO") {
      this.creditCardDetails = false
      this.chequeDetails = false;
    }
  }
