import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListInvoicesComponent } from './invoices/list-invoices/list-invoices.component';
import { AddInvoiceComponent } from './invoices/add-invoice/add-invoice.component';
import { EditInvoiceComponent } from './invoices/edit-invoice/edit-invoice.component';
import { ViewInvoiceComponent } from './invoices/view-invoice/view-invoice.component';
import { ListViewInvoiceComponent } from './invoices/list-view-invoice/list-view-invoice.component';
import { ProzInvoiceLayoutComponent } from './proz-invoice-layout/proz-invoice-layout.component';
const routes: Routes = [

  {
    path: '', component: ProzInvoiceLayoutComponent,
    children: [

      {
        path: 'addInvoice/:estimateNo',
        component: AddInvoiceComponent
      },
      {
        path: 'editInvoice',
        component: EditInvoiceComponent
      },
      {
        path: 'viewInvoice/:estimateNo',
        component: ViewInvoiceComponent
      },
      {
        path: 'listviewInvoice/:invoiceNo',
        component: ListViewInvoiceComponent
      },
      {
        path: 'listInvoices',
        component: ListInvoicesComponent
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProzInvoiceRoutingModule { }
