  gettingEstimateDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/estimateView/' + this.estimateNum, options)
      .subscribe(data => {
        var response = data.json();
        //console.log(response)
        if (response.status == 200) {
          this.estimateBasicView = response.response[0]
          console.log("estimateBasicView" + JSON.stringify(this.estimateBasicView))
          this.estimateCustomLaborData = response.customLabourDetails
          this.estimateCustomMaterialData = response.customMaterialDetails
          this.estimatePriceBookData = response.pricebookDetails
          this.totalEstimateCostData = response.totalCostDetails[0]

          this.customMLDesc = response.customDescriptionDetails
          this.finalArray = this.estimatePriceBookData.concat(this.customMLDesc)
          this.estimateViewResponse = true;
          this.viewCustomer()
          this.estimatePaymentDetails()
        }
        else {
          this.estimateViewResponse = false;
        }
        error => {
        }
      })
  }

  estimatePaymentDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getPaidAmount/' + this.estimateNum, options)
      .subscribe(data => {
        var payData = data.json();
        if (payData.status == 200) {
          this.paymentData = payData.response[0]
          console.log("paymentData" + JSON.stringify(this.paymentData))
          this.paidData = this.paymentData.paidAmount
          this.payResponseStatus = true;
        } else {
          this.paidData = 0
          this.payResponseStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }



  viewProzDetails(loginCode) {
    console.log("HI")
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/proBasicDetails/' + loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozProfileData = response.response[0];
          if (this.prozProfileData.image == '../../assets/img/no-image.png') {
            this.prozProfileData.image = this.prozProfileData.image
          } else {
            this.prozProfileData.image = Global.url + this.prozProfileData.image
          }
          this.proStatus = true;
        } else {
          this.proStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }


  viewCustomer() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getVanlynkCustomerDetails/' + this.estimateBasicView.customerId + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var customerData = data.json();
        this.customerDataInfo = customerData.customerInfo[0];
        this.customerTerms = this.customerDataInfo.terms;
        console.log("customerTerms" + JSON.stringify(this.customerTerms.split(' ')[1]))
        if (this.customerTerms == "Upon Receipt") {
          this.dueDate = this.myDate
        } else {
          this.termsValue = this.customerTerms.split(' ')[1]
          var date = new Date(this.myDate);
          var newdate = new Date(date);

          newdate.setDate(newdate.getDate() + parseInt(this.termsValue));

          var dd = newdate.getDate();
          var mm = newdate.getMonth() + 1;
          var y = newdate.getFullYear();
          this.termsDate = mm + '-' + dd + '-' + y;
          this.dueDate = this.datePipe.transform(this.termsDate, 'MM-dd-yyyy');
          // this.dueDate = mm + '-' + dd + '-' + y;
          console.log("dueDate" + this.dueDate)
        }
      })
  }

