  // generate  automatic Invoice Num
  getInvoiceNumber() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + "/generateInvoiceID", options)
      .subscribe(data => {
        this.invoiceNumInfo = data.json();
        if (this.invoiceNumInfo.status == 200) {
          this.invoiceNum = this.invoiceNumInfo.invId;
          this.dataInvInfo = true;
        } else {
          this.dataInvInfo = false;
        }
      });
  }
  // generate  automatic Invoice Num

