  getPriceBookServiceList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listPriceBookService' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.priceBookListDetails = response.response;
          this.priceBookServiceResponse = true;
        }
        else {
          this.priceBookServiceResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  // List work Order starts
  listWorkOrder(customerId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/totalWorkOrderList/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var woResponse = data.json();
        if (woResponse.status == 200) {
          //console.log("group Details" + JSON.stringify(woResponse))
          this.bill = woResponse.billingAddress;
          //console.log("list of workOrderList" + JSON.stringify(this.bill))
          this.woResponseStatus = true;
        } else {
          this.woResponseStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  // List work Order List ends


  // List work Order status List starts
  listWorkOrderStatus() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getWorkOrderStatus', options)
      .subscribe(data => {
        var woStatusResponse = data.json();
        if (woStatusResponse.status == 200) {
          //console.log("work order Details" + JSON.stringify(woStatusResponse))
          this.billStatus = woStatusResponse.response;
          //console.log("list of work order " + JSON.stringify(this.bill))
          this.woStatusResponseStatus = true;
        } else {
          this.woStatusResponseStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  // List work Order status List ends


  saveEstimate() {
    //this.router.navigate(['/proz/listEstimates'])    
    this.router.navigate(['/invoice/viewInvoice'])
  }
