  viewEstimateAnalyserService() {
    //console.log(this.estimateNum)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getEstimateAnalyserDetails/' + this.estimateNum, options)
      .subscribe(data => {
        var response = data.json();
        //console.log("response"+ JSON.stringify(response))
        if (response.status == 200) {
          this.estimateAnalyser = response.response[0]
          this.estimateAnalyserViewResponse = true;
        }
        else {
          this.estimateAnalyserViewResponse = false;
        }
        error => {
        }
      })
  }

  getChannelManagerList() {
    //console.log(this.billingAddressData.proId)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getStaffChannelManager/' + this.estimateBasicView.proId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.channelManagerList = response.response;
          console.log("in staff channel manager" + JSON.stringify(this.channelManagerList))
          this.channelManagerResponse = true;
        }
        else {
          this.channelManagerResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  getStaffName() {
    //console.log("getName" + this.addEstimate.value.channelManager)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getStaffNameDetails/' + this.addInvoice.value.channelManager, options)
      .subscribe(data => {
        var response = data.json();
        //console.log("in staff channel manager")
        if (response.status == 200) {
          this.channelManagerDetailsList = response.response[0];
          this.netProfitValue = false;
          this.grossSaleValue = true;
          this.channelManagerId = this.channelManagerDetailsList.employeeId
          this.empName = this.channelManagerDetailsList.firstName + "-" + this.channelManagerDetailsList.lastName
          this.empCommission = this.channelManagerDetailsList.compValue
          this.empCommissionType = this.channelManagerDetailsList.compType

          this.channelManagerDetailsResponse = true;
        }
        else {
          this.channelManagerDetailsResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }


